<template>
<div class="doctor">
    
    <mt-header fixed class="header" title="">
        <mt-button icon="back" slot="left" @click="goBack"></mt-button>
    </mt-header>

    <div class="content-in">
        <div style="position:relative;height: 100%;">

            <div class="item-1">
                <div class="i-icon">
                    <img :src="doctor.pic" alt="">
                </div>
                <div class="p-1">
                    <span>{{doctor.name}}</span>
                    <span>{{doctor.title}}</span>
                    <span>{{doctor.section}}</span>
                </div>
                <div class="p-2">
                    <span v-if="doctor.tag" style="display:inline-block;padding:0 10px;height:22px;line-height:22px;background-color:#5871f5;font-size:12px;color:#ffffff;text-align:center;font-style:italic;">
                    {{doctor.tag}}
                    </span>
                </div>
                <div class="p-3">
                    {{doctor.special}}    
                </div>
                
            </div>

            <div class="item-2">
                <div class="title">
                    <span></span>
                </div>
                <p class="p-content">
                    {{doctor.summary}}
                </p>
            </div>
            
            <div class="item-3">
                <div class="title">
                    <span></span>
                </div>
                <div>
                    <div class="p-2" v-for="item in doctor.record" :key="item.index">
                        <span style="font-weight: normal;">{{item.content}}</span>
                    </div>
                </div>
            </div>
    
        </div>

        
    </div>

    <div class="bottom">
        <div style="margin: 7px 15px;">
            <mt-button class="j-btn" @click="goToBook"></mt-button>    
        </div>
        
    </div>

</div>
</template>

<script>
import Vue from 'vue'
import Router from 'vue-router'
import { Cell, Button } from 'mint-ui'
import { Progress } from 'mint-ui'
import { Toast } from 'mint-ui'
import URLS from '../router/link'
import $ from 'jquery'


export default {
    components:{
        Cell,Progress
    },
    name: 'Team',
    data () {
        return {
            doctor: {
                "name": "",
                "pic": "../../static/date1.png",
                "section": "",
                "summary": "",
                "title": "",
                "tag": "",
                "special": "  ",
                "record": [
                    {
                        "content": "2015-01-01 2016-02-02   "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    },
                    {
                        "content": "2016-01-01 2017-02-02  "
                    }
                ]
            }
        }
    },
    mounted() {
        
        // this.init()
    },
    methods:{
        init() {
            const that = this;
            const id = this.$route.params.id+'';
            if(id){
                this.getDetail(id)
            } else {
                Toast({
                    message: 'id',
                    position: 'bottom',
                    duration: 3000
                });
            }
        },

        getDetail(id) {
            const url = URLS.getURL('doctorDetail');
            const data = {
                doctor_id: id,
            }
            this.doctor = {}
            $.get(url, data, res => {
                if(!res.status) {
                    this.doctor = res.data
                } else {
                    Toast({
                        message: res.message,
                        position: 'bottom',
                        duration: 3000
                    });
                }
            })
        },
        goBack() {
            this.$router.go(-1)
        },
        goToBook() {
            const hid = this.$route.params.hid
            if(hid) {
                this.$router.push({name:'book', params: { id: hid }});
            }
        },
    }

  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.doctor {
    height: 100%;
    width: 100%;
    margin: 0px;
    padding: 0px;
}
.doctor .header {
    background-color: #5871f5;
}
.content-in{
  position: absolute;
  left: 0px;
  right: 0px;
  top: 40px;
  bottom: 55px;
  overflow-y: auto;
  background-color: #ffffff;
  overflow: visible;
  z-index: 9;
}
.bottom {
    position: absolute;
    height: 55px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    border-top: 1px solid #dddddd;
}
.j-btn {
    background-color: #5871f5;
    color: #ffffff;
    width: 100%;
    border-radius: 4px;
}
.item-1 {
    height: 165px;
    box-shadow: 0px 10px 10px 0px rgba(0,0,0,0.2);
    top: 20px;
    left: 15px;
    right: 15px;
    position: absolute;
    text-align: center;
    border-radius: 10px;
}
.i-icon {
    width: 60px;
    height: 60px;
    border-radius: 30px;
    margin: -30px auto 0px;
    border: 1px solid #ffffff;

}
.i-icon img {
    height: 100%;
    width: 100%;
    border-radius: 50%;
}
.p-1 {
    font-size: 16px;
    color: #333333;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    -webkit-box-flex: 1;
    margin-top: 15px;
}
.p-2 {
    font-size: 14px;
    color: #333333;
    font-weight: lighter;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    -webkit-box-flex: 1;
    margin-top: 15px;
}
.p-3 {
    font-size: 14px;
    color: #777777;
    font-weight: lighter;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    -webkit-box-flex: 1;
    margin-top: 15px;
}
.item-2 {
    position: absolute;
    top: 210px;
    left: 15px;
    right: 15px;
    height: 150px;
}
.title {
    font-weight: bold;
}
.p-content {
    font-size: 16px;
    text-indent: 15px;
    height: 130px;
    overflow-y: auto;
}
.item-3 {
    position: absolute;
    top: 360px;
    left: 15px;
    right: 15px;
    bottom: 0px;
    overflow-y: auto;
}

</style>



