<template>
<div class="clinic-intro">
    
    <mt-header fixed class="header" title="">
        <mt-button icon="back" slot="left" @click="goBack"></mt-button>
    </mt-header>

    <div class="content-in">

        <div class="panel-in">
            <img src="../../static/date1.png" alt="">
        </div>

        <div class="content">
            <div class="i-title">
                {{info.title}}
            </div>
            <div class="i-content">
                {{info.content}}
            </div>

        </div>



    </div>

</div>
</template>

<script>
import Vue from 'vue'
import Router from 'vue-router'
import { Cell, Button } from 'mint-ui'
import { Progress } from 'mint-ui'
import { Toast } from 'mint-ui'
import URLS from '../router/link'
import $ from 'jquery'


export default {
    components:{
        Cell,Progress
    },
    name: 'ClinicIntro',
    data () {
        return {
            info: {
                title: '',
                content: "AAA\n \n " 
            }
            
        }
    },
    mounted() {
        // 
        // this.init()
    },
    methods:{
        init() {
            const that = this;
            const id = this.$route.params.id+'';
            if(id){
                this.getDetail(id)
            } else {
                Toast({
                    message: 'id',
                    position: 'bottom',
                    duration: 3000
                });
            }
        },

        getDetail(id) {
            const url = URLS.getURL('introduce');
            const data = {
                dentist_id: id,
            }
            this.info = {}
            $.get(url, data, res => {
                if(!res.status) {
                    this.info = res.data
                } else {
                    Toast({
                        message: res.message,
                        position: 'bottom',
                        duration: 3000
                    });
                }
            })
        },
        goBack() {
            this.$router.go(-1)
        },
    }

  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.clinic-intro {
    height: 100%;
    width: 100%;
    margin: 0px;
    padding: 0px;
}
.clinic-intro .header {
    background-color: #5871f5;
}
.content-in{
  position: absolute;
  left: 0px;
  right: 0px;
  top: 40px;
  bottom: 0px;
  overflow: auto;
}
.clinic-intro .panel-in {
    width: 100vw;
    height: 55.55vw;
}
.panel-in img{
  height: 100%;
  width: 100%;
}

.content {
    position: absolute;
    top: 55.55vw;
    bottom: 0px;
    left: 0px;
    right: 0px;
}
.i-title {
    height: 45px;
    line-height: 45px;
    color: #333333;
    font-size: 18px;
    padding-left: 15px;
}
.i-content {
    height: calc(100% - 60px);
    font-size: 14px;
    color: #707070;
    text-indent: 20px;
    margin: 0px 15px;
    padding-top: 10px;
    line-height: 24px;
    overflow-y: auto;
    border-top: 1px solid #dddddd;
}

</style>



